* {
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  font-style: italic;
  color: green;
}

.note {
  color: gray;
  padding-top: 4px;
  font-size: 15px;
}

.error {
  color: red;
  background-color: lightgrey;
  font-size: 20px;
  border: 2px solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
